.container {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr; /* fraction*/
  }
  
  .center {
    padding-top: 100px;
    padding-left: 10px;
  }
  
  .textbox {
    height: 600;
    width: 200;
  }


.btn {
    font-size: 1.6rem;
    font-weight: bold;
    background-color: #fff;
    border-radius: 50%;
    margin: 10px;
    width: 100px;
    height: 100px;
    opacity: 0.7;
    cursor: pointer;
    font-family: Arial, Helvetica, Helvetica Neue, sans-serif;
  }
  
  .btn:hover {
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
  }